import Vue from 'vue';
import VueI18n from 'vue-i18n';
import langEn from "./translation/en";
import langVi from "./translation/vi";

Vue.use(VueI18n);

const messages = {
    'en': langEn,
    'vi': langVi,
};

const i18n = new VueI18n({
    locale: localStorage.getItem('lang') || 'en',
    fallbackLocale: localStorage.getItem('lang') || 'en',
    messages,
});

export default i18n;