<template>
    <div class="header" :class="{'blue' : currentTheme === 'blue'}">
        <a-page-header
            :title="$t('vueProject')"
            back-icon="false"
        >
            <template slot="extra">
                <a-button
                        v-for="entry in languages"
                        :key="entry.language"
                        @click="changeLocale(entry.language)"
                        :class="{'active' : chosenLanguage === entry.language}"
                >
                    <flag :iso="entry.flag" v-bind:squared=false />
                </a-button>
            </template>
        </a-page-header>
    </div>
</template>

<script>
    import i18n from "../../plugins/i18n";
    export default {
        name: "Header",
        data() {
            return {
                languages: [
                    { flag: 'us', language: 'en' },
                    { flag: 'vn', language: 'vi' }
                ],
                chosenLanguage: i18n.locale,
            }
        },
        computed: {
            currentTheme() {
                return this.$store.getters.getTheme;
            }
        },
        methods: {
            changeLocale(locale) {
                if (i18n.locale !== locale) {
                    this.$store.dispatch('setSpinning', true);
                    localStorage.setItem('lang', locale);
                    setTimeout(() => {
                        this.$store.dispatch('setSpinning', false);
                        i18n.locale = locale;
                        this.chosenLanguage = locale;
                        this.$store.dispatch('setLanguage', locale);
                    }, 500);
                }
            }
        }
    }
</script>

<style lang="scss">
    @import "../../assets/scss/variables";
    .header {
        background: #fafcfe;
        box-shadow: 0 3px 10px rgb(0 0 0 / 10%);
        border-bottom: 1px solid #eee;
        .ant-page-header {
            padding: 7px 24px;
        }
        .ant-page-header-heading-title {
            background: $gradient;
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            font-weight: 500;
            user-select: none;
            font-family: 'Pacifico', cursive;
        }
        .ant-btn {
            background: transparent;
            border: none;
            height: auto;
            padding: 0 3px;
            filter: grayscale(0.7);
            &.active, &:hover {
                filter: grayscale(0);
            }
        }
        .ant-page-header-heading {
            display: flex;
            align-items: center;
            justify-content: space-between;
        }
        &.blue {
            .ant-page-header-heading-title {
                background: $gradientBlue;
                -webkit-background-clip: text;
                -webkit-text-fill-color: transparent;
                font-weight: 500;
                user-select: none;
                font-family: 'Pacifico', cursive;
            }
        }
    }
</style>