<template>
    <div class="sidebar" :class="{'blue' : currentTheme === 'blue'}">
        <div>
            <h4>{{$t('sidebar.title')}}</h4>
            <ul>
                <li v-for="item in data" :key="item.index">
                    <router-link :to="item.link" exact>
                        <a-icon :type="item.icon" />
                        <span>{{$t(item.name)}}</span>
                    </router-link>
                </li>
            </ul>
        </div>
        <div class="logout">
            <a-tooltip>
                <template slot="title">
                    {{$t('sidebar.logout')}}
                </template>
                <a-button @click="logOut"><a-icon type="poweroff" /></a-button>
            </a-tooltip>
            <span>{{$t('sidebar.welcome') + user}}</span>
        </div>
    </div>
</template>

<script>
    import data from "../../constant";
    import links from "../../router/links";

    export default {
        name: "Sidebar",
        data() {
            return {
                data: data.menu,
                user: this.$store.getters.userInfo,
            }
        },
        computed: {
            currentTheme() {
                return this.$store.getters.getTheme;
            }
        },
        methods: {
            async logOut() {
                await this.$store.dispatch('logOut');
                await this.$store.dispatch('saveUser', '');
                await this.$router.push(links.LOGIN);
            },
        }
    }
</script>

<style scoped lang="scss">
    @import "../../assets/scss/variables";
    .sidebar {
        padding: 10px 24px;
        h4 {
            color: #fff;
            font-weight: 400;
            text-transform: uppercase;
            font-size: 0.9rem;
        }
        ul {
            margin: 0 0 0 10px;
            padding: 0;
            list-style: none;
            li {
                padding: 4px 0;
                a {
                    color: #fff;
                    font-size: 0.82rem;
                    display: flex;
                    align-items: center;
                    & > span {
                        display: block;
                        padding: 4px 8px;
                        transition: 0.2s;
                        border-radius: 2px;
                        width: 100%;
                        margin-left: 10px;
                    }
                    &.router-link-exact-active {
                        opacity: 1;
                        & > span {
                            background: rgba(255,255,255,0.2)!important;
                        }
                    }
                    &:hover {
                        opacity: 1;
                        & > span {
                            background: rgba(255,255,255,0.1);
                        }
                    }
                }
            }
        }
        .logout {
            display: flex;
            flex-direction: column;
            align-items: center;
            button {
                background: transparent;
                border: none;
                color: #fff;
                transform: scale(1.5);
                box-shadow: none!important;
                height: auto;
                margin-bottom: 10px;
                &:hover {
                    transform: scale(2);
                }
            }
            span {
                color: #fff;
                font-size: 0.9rem;
            }
        }
        &.blue {
            background: $gradientBlueSidebar;
        }
    }
</style>