<template>
  <div id="app">
    <div class="change-theme">
      <div class="green-theme" v-if="currentTheme === 'blue'" @click="setTheme('green')">
        {{$t('button.changeTheme')}}
      </div>
      <div class="blue-theme" v-else @click="setTheme('blue')">
        {{$t('button.changeTheme')}}
      </div>
    </div>
    <div class="auth-wrapper" v-if="!isLoggedIn">
        <router-view></router-view>
    </div>
    <div class="wrapper" v-else>
      <Header />
      <a-spin :spinning="spinning">
        <div class="main">
          <div class="sidebar" :class="{'blue' : currentTheme === 'blue'}">
            <Sidebar />
          </div>
          <div class="content">
            <router-view></router-view>
          </div>
        </div>
      </a-spin>
    </div>
  </div>
</template>

<script>
import Header from './components/layouts/Header';
import Sidebar from "./components/layouts/Sidebar";

export default {
  name: 'App',
  components: {
    Header,
    Sidebar
  },
  computed: {
    spinning() {
       return this.$store.state.spinning;
    },
    isLoggedIn() {
      return this.$store.getters.isAuthenticated;
    },
    currentTheme() {
      return this.$store.getters.getTheme;
    }
  },
  methods: {
    async setTheme(theme) {
      await localStorage.setItem('theme', theme);
      await this.$store.dispatch('saveTheme', theme);
    }
  }
}
</script>

