import Vue from 'vue';
import VueRouter from "vue-router";
import Home from "../components/layouts/Home";
import Page404 from "../components/layouts/Page404";
import links from "./links";
import store from "../store";

Vue.use(VueRouter);

const routes = [
    {
        path: links.HOME,
        name: 'Home',
        component: Home,
        meta: {requiresAuth: true},
    },
    {
        path: links.NEWS_LIST,
        name: 'News list',
        component: () => import('../components/news/NewsList'),
        meta: {requiresAuth: true},
    },
    {
        path: links.CREATE_NEWS,
        name: 'Create news',
        component: () => import('../components/news/NewsForm'),
        meta: {requiresAuth: true},
    },
    {
        path: links.EDIT_NEWS,
        name: 'Edit news',
        component: () => import('../components/news/NewsForm'),
        meta: {requiresAuth: true},
    },
    {
        path: links.FEEDBACK,
        name: 'feedback',
        component: () => import('../components/feedback/Feedback'),
        meta: {requiresAuth: true},
    },
    {
        path: links.PAGE_404,
        name: 'Page 404',
        component: Page404,
        meta: {requiresAuth: true},
    },
    {
        path: links.LOGIN,
        name: 'Login',
        component: () => import('../components/auth/Login'),
        meta: { guest: true },
        beforeEnter: (to, from, next) => {
            if (store.getters.isAuthenticated) {
                return next({name: 'Home'})
            }
            next()
        }
    }
];

const router = new VueRouter({
    mode: 'history',
    routes
});

router.beforeEach((to, from, next) => {
    if (to.matched.some(record => record.meta.requiresAuth)) {
        if (store.getters.isAuthenticated) {
            next()
            return
        }
        next(links.LOGIN)
    } else {
        next()
    }
})

export default router;