import Vue from 'vue';
import Vuex from 'vuex';
import data from "../constant";

Vue.use(Vuex);

const store = new Vuex.Store({
    state: {
        news: data.rows,
        spinning: false,
        language: localStorage.getItem('lang') || 'en',
        user: localStorage.getItem('user') || '',
        theme: localStorage.getItem('theme') || 'green',
    },
    mutations: {
        createNews (state, item) {
            state.news.unshift(item);
        },
        deleteNews (state, id) {
            state.news.splice(id, 1);
        },
        setSpinning (state, value) {
            state.spinning = value;
        },
        setLanguage (state, lang) {
            state.language = lang;
        },
        saveUser (state, user) {
            state.user = user;
        },
        logOut (state) {
            state.user = '';
        },
        saveTheme (state, theme) {
            state.theme = theme;
        }
    },
    getters: {
        indexLastItem: (state) => {
            return Math.max.apply(null, state.news.map(item => item.key));
        },
        isAuthenticated: (state) => {
            return !!state.user;
        },
        userInfo: (state) => {
            return state.user;
        },
        getTheme: (state) => {
            return state.theme;
        },
        getLanguage: (state) => {
            return state.language;
        }
    },
    actions: {
        createNews ({commit}, news) {
            commit('createNews', news);
        },
        deleteNews ({commit}, item) {
            commit('deleteNews', item);
        },
        setSpinning ({commit}, value) {
            commit('setSpinning', value);
        },
        setLanguage ({commit}, lang) {
            commit('setLanguage', lang);
        },
        async saveUser ({commit}, user) {
            commit('saveUser', user);
        },
        async logOut ({commit}) {
            localStorage.removeItem('user');
            commit('logOut');
        },
        async saveTheme ({commit}, theme) {
            commit('saveTheme', theme);
        },
    }
});

export default store;